<template v-slot:fixed>
    <div class="swiper_wrapper">
        <div class="arrow_wrapper">
            <img class="left_arrow" src="../img/left_arrow.png" >
            <img class="right_arrow" src="../img/right_arrow.png" >
        </div>
        <swiper ref="mySwiper" :options="swiperOptions"
                :navigation='
                {"nextEl":".right_arrow",
                "prevEl":".left_arrow",}'
                :slidesPerView="2.4"
                :spaceBetween="75">
            <swiper-slide v-for="item in cardData" :key="item">
                <div class="card">
                    <p class="text_one">{{item.p}}</p>
                    <div class="data_card_wrapper">
                        <img :src="require(`@/img/${item.img}`)" >
                        <p class="inner_text">{{item.name}}</p>
                    </div>
                </div>
            </swiper-slide>

        </swiper>

    </div>


</template>


<script>
    import SwiperCore, { Pagination, Navigation } from 'swiper';
    import { Swiper, SwiperSlide } from "swiper/vue";
    import 'swiper/swiper-bundle.css'
    SwiperCore.use([Pagination, Navigation]);
    export default {

        props:{
            cardData:{
                type:Array
            }
        },

        components: {
            Swiper,
            SwiperSlide,
        },

        data(){
            return {

                swiperOption:{
                    slidesPerView: 5,
                    spaceBetween: 30,
                }
            }
        },

        methods: {
            onSwiper(swiper) {
                console.log(swiper);
            },
            onSlideChange() {
                console.log("slide change");
            },
        },
    };
</script>

<style scoped>
    .left_arrow, .right_arrow{
        cursor: pointer;
    }
    .arrow_wrapper .left_arrow
    {
        margin-right: 30px;
    }
    .arrow_wrapper
    {
        position: absolute;
        top: -60px;
        right: 300px;
        display: flex;
        align-items: center;
    }
    .data_card_wrapper .inner_text
    {
        margin-left: 20px;
        font-family: Roboto;
        font-style: normal;
        font-weight: bold;
        font-size: 16px;
        line-height: 30px;
        color: #402B2B;

    }
    .data_card_wrapper
    {
        position: absolute;
        display: flex;
        align-items: center;
        left: 48px;
        bottom: 40px;
    }
    .card .text_one
    {
        width: 100%;
        max-width: 320px;

        font-family: Rubik;
        font-style: normal;
        font-weight: 300;
        font-size: 16px;
        line-height: 30px;
        color: #402B2B;
    }
    .swiper_wrapper
    {
        position: relative;
        max-width: 1000px;
        margin-top: 94px;

    }
    .card
    {
        padding-top: 60px;
        padding-left: 48px;
        position: relative;
        width: 420px;
        height: 360px;
        background: #fff;
        box-shadow: 0px 20px 40px rgba(238, 77, 71, 0.1);
        border-radius: 10px;
        margin-bottom: 50px;
    }
</style>